import React from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import axios from "axios";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css"; // Import
import Aux from "../../../hoc/_Aux";
import i18n from './../../../i18n';
import i18next from "i18next";

var urlbackend = require("../../../env/env")();

class ListDriver extends React.Component {
  state = {
    Drivers: [],
  };

  componentDidMount() {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
    };

    axios
      .get(urlbackend + "/driver", config)
      .then((response) => {
        console.log("this respon list", response.data);
        this.setState({ Drivers: response.data.data });
      })
      .catch((e) => {
        console.log(e);
      });
  }

  Delette(Driver) {
    console.log("hellodelet", Driver.id);
    confirmAlert({
      title: i18n.t('title-confirm-delete'),
      message: i18n.t('ask-confirm-delete'),
      buttons: [
        {
          label: i18n.t('yes'),
          onClick: () => {
            axios
              .delete("http://localhost:5000/api/v1/Drivers/" + Driver.id)
              .then(() => {
                var array = [...this.state.Drivers];
                var index = array.indexOf(Driver);
                if (index !== -1) {
                  array.splice(index, 1);
                  this.setState({ Drivers: array });
                }
              });
          },
        },
        {
          label: i18n.t('no'),
          onClick: null,
        },
      ],
    });
    /*  */
  }
  render() {
    return (
      <Aux>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">
                  <Link to="/Add/driver">
                    {" "}
                    <Button>{i18n.t('add-driver')}</Button>{" "}
                  </Link>{" "}
                </Card.Title>
              </Card.Header>
              <Card.Body>
                <Table striped responsive>
                  <thead>
                    <tr>
                      <th>{i18n.t('fullname')}</th>
                      <th>{i18n.t('username')}</th>
                      <th>{i18n.t('email')}</th>
                      <th>{i18n.t('phone-number')}</th>
                      <th>{i18n.t('agency')}</th>
                      <th>{i18n.t('address')}</th>
                      <th>{i18n.t('descriminator')}</th>
                      <th>{i18n.t('action-event')}</th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.state.Drivers.map((Driver, index) => (
                      <tr key={Driver.id}>
                        <td>
                          {Driver.firstname} {Driver.lastname}
                        </td>
                        <td>{Driver.username} </td>
                        <td>{Driver.email} </td>
                        <td>{Driver.phone_number} </td>
                        <td>{Driver.zone?.name} </td>
                        <td> {Driver.address} </td>
                        <td> {Driver.descriminator} </td>
                        <td>
                          {" "}
                          <Button
                            variant="danger"
                            onClick={() => this.Delette(Driver)}
                          >
                            {" "}
                            <span className="fa fa-trash"></span>
                          </Button>
                          <Button
                            variant="info"
                            onClick={() => console.log("not yet")}
                          >
                            {" "}
                            <span className="fa fa-pencil"></span>
                          </Button>
                          <Link to={"/history/driver/" + Driver.id}>
                            <Button
                              variant="success"
                              onClick={() => console.log("not yet")}
                            >
                              {" "}
                              <span className="fa fa-file"></span>
                            </Button>
                          </Link>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Aux>
    );
  }
}

export default ListDriver;
